.header {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 100;
    &__container {
      display: flex;
      align-items: center;
      justify-content: space-between;
      padding-top: 36px;
      padding-bottom: 36px;
    }

    &__shop-nav {
      margin-left: 250px;
    }
}

.logo {
    display: inline-block;
    width: 117px;
    height: 22px;
    svg {
        display: block;
        width: 100%;
        height: 100%;
        fill: var(--color-dark);
        object-fit: cover;
    }
}

.catalog-header {
  padding-top: 38px;
}

.free-delivery {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: 120;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  font-size: 10px;
  font-weight: 500;
  text-transform: uppercase;
  background-color: #111;
  height: 40px;

  &__btn {
    position: absolute;
    right: 40px;
    top: 50%;
    width: 12px;
    height: 12px;
    transform: translateY(-50%);

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: var(--color-white);
    }
  }
}


.card-page {
  .header {
    padding-top: 0;
  }
}
