.breadcrumbs {
  &__list {
    display: flex;
    align-items: center;
    padding-top: 36px;
    padding-bottom: 25px;
    border-bottom: 1px solid #eee;
  }
  &__item {
    position: relative;



    &:not(:last-child) {
      margin-right: 35px;

      &::after {
        content: "/";
        position: absolute;
        right: -20px;
        top: 50%;
        transform: translateY(-50%);
        font-size: 10px;
        color: var(--color-gray);
      }
    }
  }
  &__link {
    font-weight: 500;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    &--current {
      color:var(--color-dark);
    }
  }

  & .card-prev,
  & .card-next {
    display: none;
  }
}

.card {
  .breadcrumbs .card-prev,
  .breadcrumbs .card-next {
    display: inline-block;
    color: var(--color-gray);
    font-size: 10px;
    font-weight: 700;
    text-transform: uppercase;
    transition: all 0.3s;

    &:hover {
      color: var(---color-dark);
    }
  }

  .breadcrumbs__list {
    border-bottom : none;
  }

  .breadcrumbs .container {
    display: flex;
    align-items: center;
  }

  .card-prev {
    margin-left: auto;
  }
  .card-next {
    margin-left: 20px;
  }
}
