.product {
  position: relative;
  &__img {
    position: relative;
    margin-bottom: 20px;
    img {
      max-height: 100%;
      object-fit: cover;
    }
  }

  &__sale {
    position: absolute;
    left: 20px;
    top: 20px;
    padding: 3px 10px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: #111;
    border: 2px solid var(--color-dark);
  }

  &__prop {
    margin-bottom: 10px;
  }

  &__title {
    margin: 0;
    margin-bottom: 8px;
    font-size: 13px;
    color: var(--color-tundora);

    a {
      &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
      }
    }
  }

  &__price {
    font-size: 13px;
    font-weight: 700;
  }
  &__oldprice {
    margin-left: 5px;
    font-weight: 700;
    font-size: 13px;
    color: #959595;
    text-decoration: line-through;
  }
}

.product-prop {
    display: inline-block;
    font-weight: 700;
    color: #111;
    font-size: 10px;
    padding-left: 16px;
    text-transform: uppercase;
    background-position: left center;
    background-repeat: no-repeat;
}

.new{
  background-image: url(../../img/new.png);
}
.hot {
  background-image: url(../../img/hot.png);
}
.top {
  background-image: url(../../img/top.png);
}
.only {
  background-image: url(../../img/only.png);
}
.best {
  background-image: url(../../img/best.png);
}
