.card {
  padding-top: 67px;
}

.card-content {
  padding-top: 20px;
}

.card-top{
  .container {
    display: flex;
  }

  &__ledt {
    width: 57.5%;
  }
  &__right {
    padding-top: 57px;
    padding-left: 10.7%;
    width: calc(100% - 57.5%);
  }
}

.card-info {
  &__prop {
    padding-left: 19px;
    margin-bottom: 16px;
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 500;
    font-size: 24px;
  }

  &__rate {
    margin-bottom: 23px;
    color: var(--color-gray);
    font-size: 13px;
    img {
      margin-right: 3px;
    }
  }

  &__price {
    margin-bottom: 22px;
  }

  &__descr {
    margin-bottom: 52px;
    font-size: 13px;
    line-height: 20px;
    color: var(--color-tundora);
  }

  &__first-select {
    margin-bottom: 32px;
  }
}

.info-price {
  display: flex;
  align-items: flex-end;

  &__current {
    margin-right: 20px;
    font-weight: 700;
    font-size: 24px;
  }

  &__old {
    font-size: 16px;
    color: #959595;
    text-decoration: line-through;
  }
}

.color-select {
  &__selected {
    margin: 0;
    margin-bottom: 14px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    span {
      display: inline-block;
      margin-left: 3px;
      color: var(--color-dark);
    }
  }

  &__list {
    display: flex;
    align-items: center;
    padding-left: 6px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 13px;
    }
  }

  &__btn {
    position: relative;
    width: 24px;
    height: 24px;
    border-radius: 100%;
    box-shadow: 0px 0px 3px var(--color-gray);

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      top: 50%;
      width: 32px;
      height: 32px;
      border: 1px solid #ccc;
      border-radius: 100%;
      transform: translate(-50%, -50%);
      transition: all 0.3s;
    }

    &--active,&:hover {
      &::before {
        transition: all 0.3s;
        border: 1px solid var(--color-dark);
      }
    }
  }
}


.size-select {
  &__selected {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;
    color: var(--color-gray);
    span {
      display: inline-block;
      margin-left: 3px;
      color: var(--color-dark);
    }
  }

  &__reset {
    display: none;
    align-items: center;
    color: #d24242;
    font-weight: 700;
    font-size: 10px;
    text-transform: uppercase;

    svg {
      margin-right: 4px;
      width: 9px;
      height: 9px;
      fill: #d24242;
    }
  }

  &__list {
    display: flex;
    align-items: center;
    margin-bottom: 8px;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 5px;
    }
  }

  &__btn {
    position: relative;
    width: 42px;
    height: 32px;
    font-size: 13px;
    color: var(--color-gray);
    transition: all 0.3s;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border: 1px solid #ccc;
      transition: all 0.3s;
    }

    &--active,&:hover {
      color: var(--color-dark);
      transition: all 0.3s;

      &::before {
        transition: all 0.3s;
        border: 1px solid var(--color-dark);
      }
    }
  }
}
